import React from 'react'
import './Projects.css'
const Project = ({url, imgSrc}) => {
    return (
        <div className = 'project'>
           <img alt = '' src = {imgSrc}/>
        <a href = {url} target = '_blank'><button className = 'redirectButton'>Visit Video</button></a>
        </div>
    )
}
export default Project
